<template>
  <mt-popup v-model="visible" position="bottom" class="mint-datetime">
    <div class="bg-box">
      <div class="top-bar">
        <span>{{timeTag}}</span>
        <span>{{currentTime}}</span>
        <div class="border-line"></div>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
      <footer>
        <div class="border-line-top"></div>
        <div class="foot-btn" @click="clickCancel">取消</div>
        <div class="foot-btn foot-btn-sure" @click="clickSure">确定</div>
      </footer>
    </div>
  </mt-popup>

</template>

<script>
  export default {
    name: 'time-pick',
    props: ['value', 'timeTag'],
    data() {
      return {
        slots: [
          {
            flex: 2,
            values: [],
            className: 'slot1',
            textAlign: 'center',
          }, {
            flex: 1,
            values: [],
            className: 'slot1',
            textAlign: 'center',
          },
          {
            flex: 1,
            values: [],
            className: 'slot2',
            textAlign: 'center',
          },
        ],
        slotValues: [1, 2],
        currentValue: '',
        days: [],
        dayValues: [],
        visible: false,
      };
    },
    computed: {
      currentTime() {
        return `${this.slotValues[0]} ${this.slotValues[1]}:${this.slotValues[2]}`;
      },
    },
    methods: {
      open() {
        this.visible = true;
      },
      onValuesChange(picker, values) {
        const day = this.days[this.dayValues.indexOf(values[0])];
        if (day) {
          this.currentValue = new Date(day.getFullYear(), day.getMonth(), day.getDate(), values[1], values[2]);
        }
        this.slotValues = values;
      },
      clickCancel() {
        this.visible = false;
      },
      clickSure() {
        this.$emit('confirm', this.currentValue);
        this.visible = false;
      },

      isDateString(str) {
        return /\d{4}(\-|\/|.)\d{1,2}\1\d{1,2}/.test(str);
      },

      getYear(value) {
        return this.isDateString(value) ? value.split(' ')[0].split(/-|\/|\./)[0] : value.getFullYear();
      },

      getMonth(value) {
        return this.isDateString(value) ? value.split(' ')[0].split(/-|\/|\./)[1] : value.getMonth() + 1;
      },

      getDate(value) {
        return this.isDateString(value) ? value.split(' ')[0].split(/-|\/|\./)[2] : value.getDate();
      },
      getWeekDay(value) {
        const index = this.isDateString(value) ? value.split(' ')[0].split(/-|\/|\./)[2] : value.getDay();
        switch (index) {
          case 0 :
            return '日';
          case 1 :
            return '一';
          case 2 :
            return '二';
          case 3 :
            return '三';
          case 4 :
            return '四';
          case 5 :
            return '五';
          case 6 :
            return '六';
          default:
            return 'X';
        }
      },

      getHour(value) {
        if (this.isDateString(value)) {
          const str = value.split(' ')[1] || '00:00:00';
          return str.split(':')[0];
        }
        return value.getHours();
      },

      getMinute(value) {
        if (this.isDateString(value)) {
          const str = value.split(' ')[1] || '00:00:00';
          return str.split(':')[1];
        }
        return value.getMinutes();
      },
      initSlots() {
        this.currentValue = this.value;
        if (!this.value) {
          this.currentValue = new Date();
        }
        for (let i = -15; i < 15; i++) {
          const day = new Date(this.currentValue.getTime() + i * 24 * 3600 * 1000);
          this.days.push(day);
          this.dayValues.push(`${this.getMonth(day)}月${this.getDate(day)}日周${this.getWeekDay(day)}`);
        }
        this.slots[0].values = this.dayValues;
        this.slots[0].defaultIndex = 15;
        this.slots[1].values = Array(24).fill(0).map((value, index) => {
          if (index < 10) return `0${index}`;
          return index;
        });
        this.slots[2].values = Array(60).fill(0).map((value, index) => {
          if (index < 10) return `0${index}`;
          return index;
        });
        this.slots[1].defaultIndex = this.currentValue.getHours();
        this.slots[2].defaultIndex = this.currentValue.getMinutes();
      },
    },
    mounted() {
      this.initSlots();
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .bg-box {
    width: 100%;
    footer {
      position: relative;
      .flx-x-around;
      width: 100%;
      height: 50px;
    }
  }
  .top-bar {
    position: relative;
    .flx-y-center;
    width: 100%;
    height: 80px;
    background-color: white;
    text-align: center;
    font-size: 14px;
    span {
      display: block;
      line-height: 40px;
      height: 40px;
      .text-color1;
    }
  }

  .foot-btn {
    width: 50%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 16px;
    .text-color1;
  }

  .foot-btn-sure {
    position: relative;
    color: @general-color5;
    border-left:0.5px solid @line-color;
  }

</style>



