<template>
  <div class="askLeave-container">
    <div class="yst-segmentation-line-view"></div>
    <div class="cell flx-x-between">
      <span>请假人</span>
      <span>甘宝宝</span>
    </div>
    <div class="yst-segmentation-line-view"></div>
    <div class="cell flx-x-between">
      <span>开始时间</span>
      <div @click="openBeginPicker">{{beginDate}}</div>
    </div>
    <div class="cell flx-x-between">
      <span>结束时间</span>
      <div @click="openEndPicker">{{endDate}}</div>
    </div>
    <div class="yst-segmentation-line-view"></div>
    <div class="cell flx-x-between">
      <span>请假时长</span>
      <div>请输入时长</div>
    </div>
    <div class="yst-segmentation-line-view"></div>
    <div class="leave-reason-cell">
      <span>请假事由</span>
      <textarea rows="3" placeholder="请输入请假理由" autofocus/>
    </div>
    <div class="yst-segmentation-line-view"></div>
    <div class="add-photos-cell">
      <span>图片</span>
      <div class="flx-wrap-start">
        <img class="photo-image" v-for="(item,index) in imgFiles" :src="item"/>
        <img-file-input :imgFiles="imgFiles"></img-file-input>
      </div>
    </div>
    <div class="button" @click="submit">提交</div>
    <time-picker v-model="beginTime" timeTag="开始时间" ref="beginPicker" @confirm="handleBeginConfirm"></time-picker>
    <time-picker v-model="endTime" timeTag="结束时间"  ref="endPicker" @confirm="handleEndConfirm"></time-picker>
  </div>

</template>

<script>
  import imgFileInput from '../../components/common/imgFileInput';
  import timePicker from '../../components/common/timePick';

  export default {
    name: 'yst_ask-leave',
    data() {
      return {
        imgFiles: [],
        beginTime: new Date(),
        endTime: new Date(),
        beginDate: '请选择时间',
        endDate: '请选择时间',
      };
    },
    components: { imgFileInput, timePicker },
    methods: {
      submit() {
        this.imgFiles.pop();
      },
      openBeginPicker() {
        this.$refs.beginPicker.open();
      },
      openEndPicker() {
        this.$refs.endPicker.open();
      },
      handleBeginConfirm(e) {
        console.log(e.toString());
        console.log(e.getTime());
        const hours = e.getHours() > 9 ? e.getHours() : `0${e.getHours()}`;
        const Minutes = e.getMinutes() > 9 ? e.getMinutes() : `0${e.getMinutes()}`;
        this.beginDate = `${e.getMonth() + 1}月${e.getDate()}日${hours}：${Minutes}`;
      },
      handleEndConfirm(e) {
        console.log(e.toString());
        console.log(e.getTime());
        const hours = e.getHours() > 9 ? e.getHours() : `0${e.getHours()}`;
        const Minutes = e.getMinutes() > 9 ? e.getMinutes() : `0${e.getMinutes()}`;
        this.endDate = `${e.getMonth() + 1}月${e.getDate()}日${hours}：${Minutes}`;
      },
    },
    mounted() {
      document.title = '宝宝请假';
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .askLeave-container {
    position: relative;
  }
  .cell {
    position: relative;
    width: 100%;
    .box-sizing-padding;
    .height(0.88);
    .fontSize-four;
    .text-color1;
  }
  .leave-reason-cell {
    width: 100%;
    .box-sizing-padding-wrap;
    .height(2.48);
    .fontSize-four;
  }
  .leave-reason-cell textarea {
    .height(1.2);
    .fontSize-four;
    .text-color3;
    .margin-top(0.16);
    width: 100%;
    resize:none;
    outline:none;
    border: none;
  }
  .add-photos-cell {
     width: 100%;
    .box-sizing-padding-wrap;
    .height(2.48);
    .fontSize-four;
    .text-color1;
  }
  .button {
    position: fixed;
    bottom: 8px;
  }
  .photo-image {
    .wh(1.24,1.24);
    .box-sizing-padding-wrap;
  }
</style>



